<div class="cvInfo"> <hr class="cvInfo__line"><h2 class="cvInfo__title">Work Experience </h2><div class="cvInfo__infoWrapper"><div class="cvInfo__info" v-for="item in workPlace" :key="item.id"><span class="cvInfo__date">{{item.date}}</span><p class="cvInfo__companyInfo"><a class="cvInfo__companyName" :href="item.url" target="_blank">{{item.companyName}} </a><span class="cvInfo__position">{{item.position}}</span></p><ul class="cvInfo__skils" v-if="item.responsibility"><li class="cvInfo__responsibilityText" v-for="text in item.responsibility" :key="item.id">{{text.desc}}</li></ul></div></div><hr class="cvInfo__line"><h2 class="cvInfo__title">Education Background</h2><ul class="cvInfo__skils"><li class="cvInfo__skilItem" v-for="item in study" :key="item.id"><span class="cvInfo__text">{{item.text}}</span><a class="cvInfo__text cvInfo__text--darkBlue" v-if="item.url" :href="item.url" target="_blank">{{item.name}}</a><a class="cvInfo__text cvInfo__text--darkBlue cvInfo__text--margin" v-if="item.author" v-for="author in item.author" :key="item.id" :href="author.url" target="_blank">{{author.name}}</a></li></ul><hr class="cvInfo__line"><ul class="cvInfo__skils" v-for="item in skils" :key="item.id"><h2 class="cvInfo__title">{{item.title}}</h2><li class="cvInfo__skilItem" v-for="item in item.skils" :key="item.id">{{item.skil}}</li></ul></div>